<template>
  <div class="setting">
    <van-cell center title="" :label="size | bytes">
      <template #right-icon>
        <van-button type="primary" size="small" @click="clearCache('local')"
          ></van-button
        >
      </template>
    </van-cell>
    <van-cell center title="R-18" label="">
      <template #right-icon>
        <van-switch
          :value="currentSETTING.r18"
          @input="onR18Change($event, 1)"
          size="24"
        />
      </template>
    </van-cell>
    <van-cell center title="R-18G" label="">
      <template #right-icon>
        <van-switch
          :value="currentSETTING.r18g"
          @input="onR18Change($event, 2)"
          size="24"
        />
      </template>
    </van-cell>
    <van-field
      v-model.trim="currentSETTING.api"
      label="API"
      placeholder="API"
    />
    <van-cell
      center
      title="Pixiv Viewer"
      label="https://github.com/journey-ad/pixiv-viewer"
      url="https://github.com/journey-ad/pixiv-viewer"
    ></van-cell>
    <van-cell center title="" :label="buildDate"></van-cell>
  </div>
</template>

<script>
import { Cell, Switch, Button, Dialog, Field } from "vant";
import { mapState, mapActions } from "vuex";
import { dateFormat } from "@/utils";
import { DBStorage, SessionStorage } from "@/utils/storage";
export default {
  name: "Setting",
  data() {
    return {
      buildDate: dateFormat(__BUILD_TIMESTAMP__, "yyyy-MM-dd"),
      currentSETTING: {
        api: "https://hibiapi.getloli.com/api/",
        r18: false,
        r18g: false,
      },
      size: 0,
    };
  },
  computed: {
    ...mapState(["SETTING"]),
  },
  watch: {
    $route() {
      this.calcCacheSize();
    },
  },
  methods: {
    onR18Change(checked, type) {
      let name;
      if (type === 1) name = "R-18";
      if (type === 2) name = "R-18G";

      if (checked) {
        Dialog.confirm({
          message: `${name}18`,
          confirmButtonColor: "black",
          cancelButtonColor: "#1989fa",
          closeOnPopstate: true,
        })
          .then(() => {
            if (type === 1) this.currentSETTING.r18 = checked;
            if (type === 2) {
              this.currentSETTING.r18g = checked;
              setTimeout(() => {
                Dialog.alert({
                  message: `${name}`,
                });
              }, 200);
            }
          })
          .catch(() => {
            console.log("");
          });
      } else {
        if (type === 1) this.currentSETTING.r18 = checked;
        if (type === 2) this.currentSETTING.r18g = checked;
      }
    },
    async calcCacheSize() {
      this.size = await DBStorage.size;
    },
    clearCache(type) {
      Dialog.confirm({
        message: ``,
        confirmButtonColor: "black",
        cancelButtonColor: "#1989fa",
        closeOnPopstate: true,
      }).then(async () => {
        if (type === "local") await DBStorage.clear();
        if (type === "session") SessionStorage.clear();

        this.calcCacheSize();
        this.$toast.success("");
      });
    },
    ...mapActions(["saveSETTING"]),
  },
  filters: {
    bytes(bytes) {
      bytes = Number(bytes);
      if (bytes === 0) return "0 B";

      const k = 1024;
      const dm = 0;
      const sizes = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];

      const i = Math.floor(Math.log(bytes) / Math.log(k));

      return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + " " + sizes[i];
    },
  },
  mounted() {
    this.currentSETTING = Object.assign({}, this.currentSETTING, this.SETTING);
    this.calcCacheSize();
  },
  updated() {
    this.saveSETTING(this.currentSETTING);
  },
  components: {
    [Cell.name]: Cell,
    [Button.name]: Button,
    [Switch.name]: Switch,
    [Field.name]: Field,
  },
};
</script>

<style lang="stylus" scoped>
.setting {
}
</style>